import { useEffect, useState } from "react";

const fetchDataStore = {}
/**
 * Fetch 数据
 *
 * @param {String} url 数据网址
 * @param {Object} opt 配置项
 * - time 时间戳，用于缓存和 spam
 * - option fetch 设置对象
 * - type 数据类型，默认 'json'
 * - callback 回调函数 参数为含状态数据对象
 * @return {*} 
 */
const fetchData = (url, opt)=>{
  const time = +(opt.time || new Date())
  // 清除过期数据缓存
  for(const itemUrl in fetchDataStore){
    if(time-fetchDataStore[itemUrl].time > 6e4) delete fetchDataStore[itemUrl]
  }
  // 如果有缓存数据，返回缓存
  if(fetchDataStore[url]) return fetchDataStore[url].data
  // 没有缓存则开始请求数据
  // 整理参数
  const basicOpt = {cache: 'no-cache'}
  const option = opt.option ? Object.assign(basicOpt, opt.option) : basicOpt
  const type = opt.type || 'json'
  // 缓存数据
  fetchDataStore[url] = {
    time: time,
    data: {
      status: 'Loading',
      content: {}
    }
  }
  const data = fetchDataStore[url]
  // 开始获取
  fetch(url, option)
    .then(response => {
      data.status = response.status
      if(response.status===200){
        if(type==='json') return response.json()
      }
      return response.text()
    })
    .then(newData=>{
      data.content = newData
      if(opt.callback) opt.callback(data) // 回调
    })
    .catch(error => {
      data.status = 'Error'
      data.content = error
      if(opt.callback) opt.callback(data) // 回调
    });
}

/**
 * 计时器 hook
 *
 * @param {Number} interval 时间间隔，单位 ms
 * @param {Boolean} offset 是否添加偏移量（±5秒），用来避免某些同间隔的操作过于集中
 * @return {Date} 
 */
const useTimer = (interval, offset)=>{
  const [time, setTime] = useState(new Date())
  useEffect(()=>{
    if(!interval) return
    const offsetInterval = +interval+(offset ? Math.floor(10e3*Math.random()-5e3) : 0)
    const timer = setInterval(()=>{
      setTime(new Date())
    }, offsetInterval)
    return ()=>{
      clearInterval(timer)
    }
  },[interval, offset])
  return time
}

const useWinSize = ()=>{
  // 视口宽高，用来计算网格数量等
  const [width, setWidth] = useState(window.innerWidth)
  const [height, setHeight] = useState(window.innerHeight)
  /** 窗口尺寸调整更新 */
  useEffect(()=>{
    const resize = ()=>{
      setWidth(window.innerWidth)
      setHeight(window.innerHeight)
    }
    window.addEventListener('resize', resize)
    return ()=>{
      window.removeEventListener('resize', resize) 
    }
  },[])
  return {
    width,
    height
  }
}

/**
 * 把驼峰写法转换为连字符写法
 *
 * @param {String} str
 */
const case2Line = str => str.replace(/([A-Z])/g, '-$1')
 .replace(/^-/g, '')
 .toLowerCase()

/**
 * className 生成器
 *
 * @param {String|Array|Object} args 可以是字符串，数组，对象
 * @return {String} 
 */
const classNames = (...args)=>{
  const namesArr = []
  args.forEach(arg=>{
    if(typeof(arg)==='string'){
      namesArr.push(arg.split(/\s+/g))
      return
    }
    if(arg instanceof Array){
      namesArr.push(arg)
      return
    }
    if(arg instanceof Object){
      namesArr.push(Object.keys(arg).filter(e=>arg[e]))
      return
    }
  })
  return namesArr.flat().join(' ')
}

/**
 * 获取对象深层数据
 *
 * @param {Object|Array} obj 数据对象
 * @param {String} path 数据路径
 * @param {*} defaultVal 默认值（可选）
 * @return {*} 
 */
const dataGetter = (obj, path, defaultVal)=>{
  const pathArr = path.split('.')
  const data = pathArr.reduce(
    (objCurr, key)=> objCurr===undefined ? undefined : objCurr[key]
  , obj)
  return data===undefined ? defaultVal : data
}

export {
  fetchData,
  useTimer,
  useWinSize,
  case2Line,
  classNames,
  dataGetter,
}