import React, { useMemo } from 'react';
import { classNames } from '../functions';
import './Card.scss'

function Card (props) {
  const className = useMemo(()=>classNames(
    'card',
    (props.type || 'normal'),
    {
      'full': props.isFull || props.isBackground,
      'background': props.isBackground
    }
    ), [props.type, props.isFull, props.isBackground])
  const isLine = useMemo(()=>props.type==='verticalLine' || props.type==='horizontalLine', [props.type])
  const style = useMemo(()=>Object.assign({
      gridArea: props.pos.join('/'),
    }, props.style),[props.style, props.pos])
  const lineStyle = useMemo(()=>{
    if(isLine){
      const newStyle = {}
      newStyle.background = props.style.background
      newStyle.borderRadius = props.style.borderRadius
      if(props.style.border){
        const borderParams = props.style.border.split(/\s/g)
        if(props.type==='verticalLine') newStyle.width = borderParams[0]
        if(props.type==='horizontalLine') newStyle.height = borderParams[0]
        if(borderParams[2]) newStyle.background = borderParams[2]
      }
      return newStyle
    }
  },[isLine, props.style, props.type])
  return (
    <div
      className={className}
      style={style}
      onClick={props.clickHandler}
      >
      { isLine ? <div className='line' style={lineStyle}></div> : props.children}
    </div>
  )
}

export default Card;