import React, { useCallback, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Panel.scss'
import widgets from '../widgets/widgets'
import {case2Line, classNames} from '../functions'
import Card from './Card';

const about = [
  {type: 'title', name: 'about-title', text: '关于：'},
  {name: 'project-home', title: '🏠项目主页', href: 'https://github.com/dmscode/Dashboard-on-desk'},
  {name: 'donations-coffee', title: '☕捐助咖啡', href: 'https://afdian.net/@daomishu'},
] 

function Panel (props) {
  /** 是否导航页面 */
  const isNav = useMemo(()=>Boolean(props.routers), [props.routers])
  const [whichFullscreen, setWhichFullscreen] =useState('')
  const navigate = useNavigate()
  /** 面板配置对象 */
  const panelConfig = useMemo(()=>Object.assign({}, props.config.global, (props.config.panels[props.name] || {})), [props.config.global, props.config.panels, props.name])
  /** 格子尺寸 */
  const gridSize = useMemo(()=>+panelConfig.gridSize, [panelConfig.gridSize])
  /** 格子间隔 */
  const gap = useMemo(()=>panelConfig.gap || gridSize/2, [panelConfig.gap, gridSize])
  /** 列数 和 行数 */
  const cols = useMemo(()=>Math.floor( (props.size.width-gap)/(gridSize+gap) ), [props.size.width, gridSize, gap])
  const rows = useMemo(()=>Math.floor( (props.size.height-gap)/(gridSize+gap) ), [props.size.height, gridSize, gap])
  /** 内补计算函数 */
  const getPadding = useCallback(val=>{return (val-gap)%(gridSize+gap)/2+gap}, [gridSize, gap])
  const topPadding = useMemo(()=>getPadding(props.size.height), [getPadding, props.size.height])
  const leftPadding = useMemo(()=>getPadding(props.size.width), [getPadding, props.size.width])
  /** 网格尺寸（宽高）计算 */
  const getGridSize = useCallback(g=>{return g ? g*(gridSize+gap)-gap : 0}, [gridSize, gap])
  /** 网格位置（上下左右）计算 */
  const getGridPos = useCallback((g, isLeft=true)=>{return g*(gridSize+gap)+(isLeft ? leftPadding : topPadding)}, [gridSize, gap, leftPadding, topPadding])
  /** 面板样式（动态网格） */
  const panelStyle = {
    gap,
    padding: topPadding+'px '+leftPadding+'px',
    gridTemplateColumns: 'repeat(auto-fill, '+gridSize+'px)',
    gridTemplateRows: 'repeat(auto-fill, '+gridSize+'px)',
    background: panelConfig.background
  }

  /**
   * 计算无法使用 grid 布局时的卡片定位样式
   * 
   * @param {Object} conf
   * - noGrid 浏览器不支持 Grid 布局
   * - left 左边起始的格子数
   * - top 顶部起始的格子数
   * - right 右侧结束的格子数
   * - bottom 底部结束的格子数
   * - width 宽几格
   * - height 高几格
   * - sizeWidth 真实宽度
   * - sizeHeight 真实高度
   * - background 背景设定
   * - blur 背景模糊设定
   * - border 边框设定
   * - borderRadius 圆角设定
   * @return {Object} 
   */
  const getCardStyle = useCallback(conf=>{
    const style = {
      background: conf.background,
      border: conf.border,
      borderRadius: conf.borderRadius
    }
    if(conf.noGrid) Object.assign(style, {
      position: 'absolute',
      left: getGridPos(conf.left)+'px',
      top: getGridPos(conf.top, false)+'px',
      width: conf.sizeWidth
              ? conf.sizeWidth+'px'
              : getGridSize(conf.right ? conf.right-conf.left : conf.width),
      height: conf.sizeHeight
              ? conf.sizeHeight+'px'
              : getGridSize(conf.bottom ? conf.bottom-conf.top : conf.height),
    })
    if(conf.blur){
      style.backdropFilter = 'blur('+conf.blur+'px)'
      style.webkitBackdropFilter = 'blur('+conf.blur+'px)'
    }
    return style
  }, [getGridSize, getGridPos])

  /**
   * 输出导航列表
   *
   * @return {*} 
   */
  const getNavs = useCallback(()=>{
    const panels = props.routers.filter(r=>r.type==='panel')
    const tools = props.routers.filter(r=>r.type==='tool')
    const navs = [].concat(
      (panels.length ? [{type: 'title', name: 'panels-title', text: '面板：'}] : []),
      panels,
      (tools.length ? [{type: 'title', name: 'tools-title', text: '工具：'}] : []),
      tools,
      about
    )
    let col = 1
    let row = 1
    const width = +panelConfig.navWidth || Math.ceil(160/panelConfig.gridSize)
    const height = +panelConfig.navHeight || Math.ceil(48/panelConfig.gridSize)
    const nextRow = ()=>{
      row += height
      col = 1
    }
    const nextNav = ()=>{
      col += width
      if(col+width > cols) nextRow()
    }
    const Title = widgets.Title
    const Nav = widgets.Nav
    return navs.map(n=>{
      if(n.type==='title'){
        if(row!==1 || col !== 1) nextRow()
        const style = getCardStyle({
          noGrid: props.noGrid,
          left: col,
          top: row,
          width,
          height,
          background: panelConfig.cardBackground,
          blur: panelConfig.cardBlur,
          border: panelConfig.cardBorder,
          borderRadius: panelConfig.cardRadius,
        })
        const nav = (<Card key={n.name} pos={[row, col, row+height, col+width]} type={panelConfig.cardType} style={style}>
          <Title config={{content: n.text}} className="nav-link"></Title>
        </Card>)
        nextNav()
        return nav
      }
      const style = getCardStyle({
        noGrid: props.noGrid,
        left: col,
        top: row,
        width,
        height,
        background: panelConfig.cardBackground,
        blur: panelConfig.cardBlur,
        border: panelConfig.cardBorder,
        borderRadius: panelConfig.cardRadius,
      })
      const nav = (<Card key={n.name} pos={[row, col, row+height, col+width]} type={panelConfig.cardType} style={style}>
        <Nav config={n}></Nav>
      </Card>)
      nextNav()
      return nav
    })
  }, [props.routers, props.noGrid, panelConfig, cols, getCardStyle])

  /**
   * 整理小部件位置
   *
   * @return {Array} 
   */
   const getPos = useCallback((pos)=>{
    const newPos = [pos.top, pos.left, pos.bottom, pos.right]
    if(!newPos[2]) newPos[2] = pos.height ? pos.top+pos.height : rows
    if(!newPos[3]) newPos[3] = pos.width ? pos.left+pos.width : cols
    return newPos
  }, [cols, rows])

  /**
   * 获取工具对应的配置
   *
   * @return {Object} 
   */
  const getToolConf = useCallback((tConfig)=>{
    const toolConfig = Object.assign(
      {},
      props.config.global,
      props.config.tools[tConfig.name],
      props.config.panels[props.name],
      tConfig
    )
    delete toolConfig.tools
    return toolConfig
  }, [props.config, props.name])

  const logoCardLength = useMemo(()=>Math.floor((128+gap)/(gridSize+gap)), [gridSize, gap])
  const logoCard = useMemo(()=>{
    const cardArr = [
      {
        config: {
          image: '/images/logo.png',
          path: '/'
        }
      },
      {
        config: {
          image: '/images/afdian.png',
          href: 'https://afdian.net/@daomishu'
        }
      }
    ]
    const isVertical = rows > cols
    cardArr.forEach((c, i)=>{
      c.pos = getPos({
        left: isVertical ? cols-logoCardLength : cols-(cardArr.length-i)*logoCardLength,
        top: isVertical ? rows-(cardArr.length-i)*logoCardLength : rows-logoCardLength,
        width: logoCardLength,
        height: logoCardLength
      })
      c.style = getCardStyle({
        noGrid: props.noGrid,
        left: c.pos[1],
        top: c.pos[0],
        right: c.pos[3],
        bottom: c.pos[2],
        background: panelConfig.background,
        blur: panelConfig.cardBlur,
        border: panelConfig.cardBorder,
        borderRadius: panelConfig.cardRadius,
      })
    })
    return cardArr
  }, [cols, rows, logoCardLength, panelConfig, props.noGrid, getPos, getCardStyle])
  /**
   * 输出面板中的小部件
   *
   * @return {*} 
   */
  const getTools = useCallback(
    ()=>{
      const Nav = widgets.Nav
      const defaultCards = logoCard.map((c, i)=><Card
          key={i}
          pos={c.pos}
          style={c.style}
          >
            <Nav config={c.config}></Nav>
        </Card>)
      const userCards = props.config.panels[props.name].tools.map((t, i)=>{
        const toolConfig = getToolConf(t)
        const mark = i+'-'+t.name
        const isFull = whichFullscreen === mark
        const isBackground = toolConfig.beBackground
        const TheComponent = toolConfig.name && widgets[toolConfig.name]
        const pos = getPos(toolConfig.pos)
        const size = (isFull || isBackground) ? props.size : {
          width:  getGridSize(pos[3]-pos[1]),
          height: getGridSize(pos[2]-pos[0])
        }
        const clickHandler = ()=>{
          if(!t.name || isBackground) return
          if(toolConfig.canBeFullscreen){
            setWhichFullscreen(f=>{
              return f===mark ? '' : mark
            })
            return
          }
          if(toolConfig.linkToSelf){
            navigate('/'+case2Line(t.name))
            return
          }
          if(toolConfig.linkToUrl){
            window.open(toolConfig.linkToUrl)
            return
          }
        }
        const style = getCardStyle({
          noGrid: props.noGrid && !isFull && !isBackground,
          left: pos[1],
          top: pos[0],
          sizeWidth: size.width,
          sizeHeight: size.height,
          background: toolConfig.cardBackground,
          blur: toolConfig.cardBlur,
          border: toolConfig.cardBorder,
          borderRadius: toolConfig.cardRadius,
        })

        return (
          <Card
            key={i}
            pos={pos}
            type={toolConfig.cardType}
            clickHandler={clickHandler}
            isFull={isFull}
            isBackground={isBackground}
            style={style}
            >
            {TheComponent && <TheComponent
                      size = {size}
                      config = {toolConfig}
                    />}
          </Card>
        )
      })
      return (<>{defaultCards}{userCards}</>)
  }, [props.config, props.name, props.size, props.noGrid, whichFullscreen, logoCard, getPos, getToolConf, navigate, getGridSize, getCardStyle])

  return (
    <div className={classNames('panel', props.name)} style={panelStyle}>
      {
        isNav
          ? getNavs()
          : getTools()
      }
    </div>
  )
}

export default Panel
