import React, { useMemo } from 'react';
import useStore from '../store';
import { classNames } from "../functions"
import './Clock.scss';

function NumCard (props) {
  const num = props.num<=9 ? '0'+props.num : String(props.num)
  return (
    <div className={classNames('num-card text-center', props.className)} style={props.style}>{num}</div>
  )
}
function SepCard (props) {
  const className = 'sep-card' + (props.show ? '' : ' hide')
  return (
    <div className={className}>:</div>
  )
}

function NumClock (props){
  const seconds = useStore('seconds')
  const minutes = useStore('minutes')
  const hours = useStore('hours')

  const heightScale = 2
  const sepShow = seconds%2

  const widthScale = useMemo(()=>props.config.seconds ? 8 : 5, [props.config.seconds])

  const width = props.size.width
  const height = props.size.height ? props.size.height : width/widthScale*heightScale
  
  const baseLength = width/height < widthScale/heightScale ? width/widthScale : height/heightScale
  const clockStyle = {
    width: width+'px',
    height: height+'px',
    fontSize: baseLength*heightScale+'px'
  }
  return (
    <div
      className={
        classNames(
          'num-clock letter-info flex-box flex-pos-5',
          { ink: props.config.ink }
          )
      }
      style={clockStyle}>
      <NumCard className='hours' num={hours} />
      <SepCard show={sepShow} />
      <NumCard className='minutes' num={minutes} />
      {props.config.seconds && <SepCard show={sepShow} />}
      {props.config.seconds && <NumCard className='seconds' num={seconds} />}
    </div>
  )
}
function VerticalClock (props){
  const minutes = useStore('minutes')
  const hours = useStore('hours')

  const scale = 0.625 // 2/3.2

  const width = props.size.width
  const height = props.size.height ? props.size.height : width/scale
  
  const baseLength = (width/height < scale ? width/scale/2 : height*scale)
  const clockStyle = {
    width: width+'px',
    height: height+'px',
    fontSize: baseLength+'px'
  }
  return (
    <div
      className={classNames('vertical-clock letter-info flex-box flex-pos-5', { ink: props.config.ink })}
      style={clockStyle}>
      <NumCard className='hours' num={hours} />
      <NumCard className='minutes' num={minutes} />
    </div>
  )
}

export {
  NumClock,
  VerticalClock
}
