import React, { useEffect, useState } from 'react';
import { Routes, Route } from "react-router-dom";
import { fetchData, useTimer, useWinSize, case2Line, classNames } from "./functions"
import Panel from './Components/Panel'
import widgets from './widgets/widgets'

import './App.scss';

/**
 * 获取对应工具的【默认】配置对象
 *
 * @param {String} toolName 工具的名称
 * @return {Object} 配置对象，如未获取到则为空对象
 */
const getToolConf = (config, toolName)=>{
  if(!config || !config.global || !config.tools || !config.tools[toolName]) return {}
  return Object.assign({}, config.global, config.tools[toolName] )
}

/**
 * 从一个对象创建相关路由
 *
 * @param {Object} obj 配置中的相关对象
 * @param {String} name 名称
 * @param {String} type 类型
 * @return {Object} 
 */
const createRouter = (obj, name, type)=>{
  const r = {
    name,
    title: obj.title ? obj.title : name,
    type,
    path: case2Line(name)
  }
  if(type==='panel'){
    if(name==='index') r.path = '/'
    if(!obj.tools || !obj.tools.length) r.isNav = true
  }
  return r
}

/**
 * 获取路由数组
 *
 * @param {Object} data 配置对象
 * @return {Array} 
 */
const getRouters = data=>{
  const routersArr = []
  if(!data.panels || !data.panels.index){
    routersArr.push({
      name: 'index',
      title: '工具索引',
      type: 'panel',
      path: '/',
      isNav: true
    })
  }
  return routersArr.concat(
    (data.panels ? Object.keys(data.panels).map(name=>createRouter(data.panels[name], name, 'panel')) : []),
    (data.tools ? Object.keys(data.tools).map(name=>createRouter(data.tools[name], name, 'tool')) : []),
  )
}

function App(props){
  const noGrid = !window.getComputedStyle(document.body).grid
  const [interval, setInterval] = useState(6e4)

  const time = useTimer(interval)

  const winSize = useWinSize()

  // 路由数组，也是导航项数组
  const [routersArr, setRoutersArr] = useState([])
  // 全局配置信息，通过读取配置文件获得
  const [config, setConfig] = useState()
  const [theme, setTheme] = useState(()=>{
    const hours = time.getHours()
    return (hours<6 || hours>=20) ? 'dark' : 'light'
  })

  const [userColors, setUserColors] = useState('')

  useEffect(()=>{
    fetchData('./config.json', {
      time: new Date(),
      callback: data=>{
        if(data.status===200){
          setConfig(data.content)
        }
      }
    })
  }, [time, setConfig])
  /** 主题切换 */
  useEffect(()=>{
    if(!config || !config.global || !config.global.theme) return
    if(config.global.theme !== 'auto'){
      setTheme(config.global.theme)
      return
    }
    const themeToggleTime = (config.global.themeToggleTime && config.global.themeToggleTime.length === 2) ? config.global.themeToggleTime : [6, 20]
    const nowTime = time.getHours()+time.getMinutes()/60
    setTheme( (nowTime<themeToggleTime[0] || nowTime>=themeToggleTime[1]) ? 'dark' : 'light' )
  }, [config, time])

  /** 配置文件获取和处理 */
  useEffect(()=>{
    if(config && config.tools) setRoutersArr(getRouters(config))
    if(config && config.global && typeof(config.global.interval)!=='undefined') setInterval(config.global.interval)
  },[config])
  /** 颜色样式 */
  useEffect(()=>{
    if(!config || !config.colors) return
    let styleStr = ''
    for(const name in config.colors){
      styleStr+= name==='light' ? ':root {\n' : '.'+name+' {\n'
      for(const c in config.colors[name]){
        styleStr += '--'+c+':'+config.colors[name][c]+';\n'
      }
      styleStr += '}\n'
    }
    setUserColors(styleStr)
  },[config])

  return (
    <div className={classNames('App', theme)}>
      {routersArr.length
        ? (
        <Routes>
          {routersArr.map(r=>{
              if(r.type==='panel'){
                return <Route
                  path={r.path}
                  key={r.name}
                  element={<Panel
                    name={r.name}
                    size = {winSize}
                    routers={r.isNav && routersArr}
                    config={config}
                    noGrid={noGrid}
                  />}
                />
              }
              const TheComponent = widgets[r.name]
              return <Route
                path={r.path}
                key={r.name}
                element={<TheComponent
                  size = {winSize}
                  config = {getToolConf(config, r.name)}
                />}
              />
            }
            )}
        </Routes>
      )
      : (<>Loading</>)}
      <style>{userColors}</style>
    </div>
  )
}

export default App;