import { useState, useEffect } from 'react'

const data = {}
const keys = [
  'time',      // 时间对象
  'timeStamp', // 时间戳
  'seconds',   // 秒
  'minutes',   // 分
  'hours',     // 小时
  'year',      // 年
  'month',     // 月
  'date',      // 日
  'day',       // 星期
]
const listeners = {}

keys.forEach(key=>{
  listeners[key] = [] // 初始化监听器
})
const runListener = type=>{
  listeners[type].forEach(e => e(data[type]));
}

const useStore = (key)=>{
  const [value, newListener] = useState(data[key])
  useEffect(() => {
    // newListener是新的订阅者
    listeners[key].push(newListener)
    // 组件卸载取消订阅
    return () => {
      listeners[key] = listeners[key].filter((listener) => listener !== newListener)
    }
  }, [key])
  
  return value
}

const dataSetter = (key, value)=>{
  if(data[key]===value) return
  data[key] = value
  runListener(key)
}

const refreshTime = ()=>{
  const time = new Date()
  const timeStamp = time.getTime()
  const seconds = time.getSeconds()
  dataSetter('time', time)
  // 如果时间戳和上一个时间戳差距大于等于 10 秒，则视为脚本被休眠了
  const noSleep = timeStamp-data.timeStamp < 1e4
  // 如果当前秒数大于等于上一次的秒数，说明分钟数未改变
  const sameMinute = seconds >= data.seconds
  dataSetter('timeStamp', timeStamp)
  dataSetter('seconds', seconds)
  // 如果没有休眠，并且分钟数未变化，则不需要更新后面的数据了
  if(noSleep && sameMinute) return
  const minutes = time.getMinutes()
  const sameHours = minutes >= data.minutes
  dataSetter('minutes', minutes)
  if(noSleep && sameHours) return
  const hours = time.getHours()
  dataSetter('hours', hours)
}

refreshTime()

window.setInterval(()=>{
  setTimeout(()=>{
    refreshTime()
  }, 1e3-Date.now()%1e3)
}, 900)

export default useStore