import React, { useCallback, useMemo, useEffect, useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import { classNames } from "../functions"

/**
 * 标题组件
 *
 * @param {Object} props
 * - className
 * - config
 *  - align
 *  - content
 * @return {*} 
 */
function Title(props){
  return (
    <div className={classNames('flex-box', props.config.align || 'flex-pos-4')}>
      <h3 className={classNames('title', props.className)}>{props.config.content}</h3>
    </div>
  )
}
/**
 * 导航（链接）组件
 *
 * @param {Object} props
 * - className
 * - config
 *  - align
 *  - path
 *  - href
 *  - title
 *  - image
 * @return {*} 
 */
function Nav(props){
  const className = useMemo(()=>classNames(
      'nav-link',
      props.className,
      props.config.align && 'text-'+props.config.align,
      props.config.image && 'max-image'
    ), [props.className, props.config.align, props.config.image])
  const content = useMemo(()=>{
    return props.config.image
        ? <></>
        : props.config.title
  }, [props.config.image, props.config.title])
  const style = useMemo(()=>{ return props.config.image ? { backgroundImage: 'url("'+props.config.image+'")' } : {} }, [props.config.image])
  return props.config.path
      ? <Link className={className} to={props.config.path} style={style}>{content}</Link>
      : <a className={className} href={props.config.href} target="_blank" rel="noreferrer" style={style}>{content}</a>
}

/**
 * 状态组件，仅用于 card 之内
 *
 * @param {Object} props
 * - state 状态名称
 * - stateStyle 【可选】状态样式对象，内部为每个样式对应的背景
 * - progress 进度，如果有 state 属性，则进度无效
 * - progressLevel 【可选】不同进度对应的状态名称，数值为 progress 每个等级的结束节点
 * - min 【可选】进度数据的最小值
 * - max 【可选】进度数据的最大值
 * - type 【可选】状态显示的类型，可以设置为背景（background）、圆点（point）和角标（flag）模式，如果设定，则进度不会显示进度条，即：进度会被单纯的转换为状态显示
 * @return {*} 
 */
function State(props){
  const ProgressLevel = useMemo(()=> props.progressLevel ? props.progressLevel : {
    0: 'start',
    99: 'doing',
    100: 'end',
  }, [props.progressLevel])
  const min = useMemo(()=>props.min || 0, [props.min])
  const max = useMemo(()=>props.max || 100, [props.max])
  const getLevel = useCallback(progress=>{
    const levels = Object.keys(ProgressLevel).map(e=>Number(e)).sort((a,b)=>b-a)
    let sta
    for(const l of levels){
      if(+progress<= l) sta = l
    }
    return ProgressLevel[String(sta)]
  },[ProgressLevel])

  const style = props.type ? {} : Object.assign({}, props.size)
  const state = useMemo(()=>props.state ? props.state : getLevel(props.progress), [props.state, props.progress, getLevel])
  if(props.stateStyle && props.stateStyle[state]) style.background = props.stateStyle[props.state]
  if(props.progress && props.type!=='background') style.width = props.progress/(max-min)*100+'%'
  return (
    <div className={classNames('state', state, props.type)} style={style}></div>
  )
}
/**
 * 最大单行文本
 *
 * @param {*} props
 * - content 内容
 * - size
 *   - width 容器宽度
 *   - height 容器高度
 * @return {*} 
 */
const MaxFontSingleLine = (props)=>{
  const [fontSize, setFontSize] = useState(100)
  const el = useRef(null)
  useEffect(()=>{
    const elWidth = el.current.scrollWidth || props.size.width
    const elHeight = el.current.scrollHeight || props.size.height
    const scale = Math.min(props.size.width/elWidth, props.size.height/elHeight)
    setFontSize(preSize=>preSize*scale)
  }, [props])
  const style =  {
    lineHeight: "1em",
    fontSize: fontSize+'px'
  }
  return (
    <span className="max-font-single-line" ref={el} style={style}>{props.content}</span>
  )
}

export {
  Title,
  Nav,
  State,
  MaxFontSingleLine,
}