import React, { useEffect, useMemo, useState } from 'react';
import md5 from 'md5'
import { useTimer, fetchData, dataGetter, classNames } from "../functions"
import { State, MaxFontSingleLine } from "./Decoration"

/**
 * 基础数据展示
 *
 * @param {*} props
 * - className
 * - data 要显示的数据内容
 * - color 文字颜色
 * - size 容器尺寸
 * - state 状态
 * - trueBackground 有数据时的背景颜色
 * @return {*} 
 */
function EasyDataShower(props){
  const className = classNames(
    'data-shower',
    'letter-info',
    'flex-box',
    'flex-pos-5',
    props.className
  )
  return (
    <div
      className={className}
      style={ {color: props.color} }>
      <MaxFontSingleLine
        content={props.data}
        size={props.size}
      ></MaxFontSingleLine>
      <State
        state={props.state}
        stateStyle={props.trueBackground && {'info': props.trueBackground}}></State>
    </div>
  )
}

/**
 * 简单 JSON 数据展示
 *
 * @param {*} props
 * - config
 *   - href 数据地址
 *   - interval 更新间隔
 *   - path 数据路径
 *   - color 文字颜色
 *   - onTrue 当数据为真时设定显示状态
 *   - trueBackground 数据为真时的背景颜色
 * @return {*} 
 */
function EasyJson (props) {
  const [data, setData] = useState('')
  const [state, setState] = useState('')
  const time = useTimer( props.config.interval, true)
  useEffect(()=>{
    fetchData(props.config.href, {
      time,
      callback: d=>{
        if(d.status===200){
          setData(pre=>{
            const newData = dataGetter(d.content, props.config.path)
            setState(props.config.onTrue && +newData ? 'info' : '')
            return newData
          })
          return
        }
        setState('error')
        setData(d.status)
      }
    })
  }, [props.config.href, props.config.path, props.config.onTrue, time])
  return <EasyDataShower
          data={data}
          className={'easy-json'}
          color={props.config.color}
          size={props.size}
          state={state}
          trueBackground={props.config.trueBackground}
          ></EasyDataShower>
}

/**
 * Slowly 数据
 *
 * @param {*} props
 * @return {*} 
 */
function SlowlyNotic (props) {
  const [data, setData] = useState('')
  const [incoming, setIncoming] = useState(0)
  const [unreading, setUnreading] = useState(0)
  const [state, setState] = useState('')
  const time = useTimer(props.config.interval, true)
  useEffect(()=>{
    fetchData(props.config.incomingHref+props.config.token, {
      time,
      callback: d=>{
        if(d.status===200){
          setIncoming(dataGetter(d.content, 'incoming'))
          return
        }
        if(d.status===401){
          setIncoming(401)
        }
      }
    })
  }, [props.config.incomingHref, props.config.token, time])
  useEffect(()=>{
    fetchData(props.config.unreadingHref+props.config.token, {
      time,
      callback: d=>{
        if(d.status===200){
          setUnreading(dataGetter(d.content, 'unreadCounter'))
          return
        }
        if(d.status===401){
          setUnreading(401)
        }
      }
    })
  }, [props.config.unreadingHref, props.config.token, time])
  useEffect(()=>{
    setData((!incoming && !unreading) ? 'Slowly' : incoming+' / '+unreading)
    if(incoming===401 && unreading===401) {
      setState('error')
      return
    }
    setState(Number(incoming)+Number(unreading) ? 'info' : '')
  }, [incoming, unreading])
  return <EasyDataShower
          data={data}
          className={'slowly-notic'}
          color={props.config.color}
          size={props.size}
          state={state}
          trueBackground={props.config.trueBackground}
          ></EasyDataShower>
}
/**
 * 小众论坛
 *
 * @param {*} props
 * @return {*} 
 */
function MetaNotic (props) {
  const [data, setData] = useState('')
  const [state, setState] = useState('')
  const time = useTimer(props.config.interval, true)
  useEffect(()=>{
    fetchData(props.config.href, {
      time,
      callback: d=>{
        if(d.status===200){
          let count = 0
          const notifications = dataGetter(d.content, 'notifications')
          if(notifications && notifications.length){
            notifications.forEach((e, i)=>{
              if(!e.read) count++
            })
            setData(count)
            setState(props.config.onTrue && count ? 'info' : '')
          }
          return
        }
        setState('error')
        setData(d.status)
      }
    })
  }, [props.config.href, props.config.onTrue, time])
  return <EasyDataShower
          data={data}
          className={'meta-notic'}
          color={props.config.color}
          size={props.size}
          state={state}
          trueBackground={props.config.trueBackground}
          ></EasyDataShower>
}
/**
 * 爱发电
 *
 * @param {*} props
 * @return {*} 
 */
function AfdianNotic (props) {
  const [data, setData] = useState('')
  const [state, setState] = useState(false)
  const time = useTimer(props.config.interval, true)
  const ts = useMemo(()=> time && Math.round((+new Date())/1e3), [time])
  const params = useMemo(()=>JSON.stringify({page: 1}), [])
  const opt = useMemo(()=>{
    return {
      method: 'post',
      body: JSON.stringify({
        user_id: props.config.user_id,
        ts,
        params,
        sign: md5(props.config.token+'params'+params+'ts'+ts+'user_id'+props.config.user_id)
      }),
      headers: {
        'Content-Type': 'application/json'
      },
      cache: 'no-cache',
    }
  }, [props.config.user_id, props.config.token, ts, params])
  useEffect(()=>{
    fetchData(props.config.href, {
      time,
      option: opt,
      callback: d=>{
        if(d.status===200){
          const newData = dataGetter(d.content, props.config.path)
          setData(oldData=>{
            if(props.config.onTrue ) setState((oldData!=='' && oldData!=='undefined' && oldData!==newData) ? 'info' : '')
            return newData
          })
          return
        }
        setState('error')
        setData(d.status)
      }
    })
  }, [props.config.href, props.config.path, props.config.onTrue, time, opt])

  return <EasyDataShower
          data={data}
          className={'afdian'}
          color={props.config.color}
          size={props.size}
          state={state}
          trueBackground={props.config.trueBackground}
          ></EasyDataShower>
}

export {
  EasyJson,
  SlowlyNotic,
  MetaNotic,
  AfdianNotic
}