import React from 'react';
import { useWinSize } from '../functions'

function GridInfo (props){
  const winSize = useWinSize()
  /** 格子尺寸 */
  const gridSize = +props.config.gridSize
  /** 格子间隔 */
  const gap = props.config.gap || gridSize/2
  /** 列数 和 行数 */
  const cols = Math.floor( (winSize.width-gap)/(gridSize+gap) )
  const rows = Math.floor( (winSize.height-gap)/(gridSize+gap) )
  return (
    <>
      <div>width: {cols}</div>
      <div>height: {rows}</div>
    </>
  )
}
export default GridInfo