import React, { useEffect, useState } from 'react';
import { useTimer } from "../functions"
import './RandomPic.scss'

const getImage = (string, replaceObj)=>{
  return string.replace(/\{(\w+)\}/g, (m, s)=>{
    return replaceObj[s] ? replaceObj[s] : m
  })
}

function RandomPic (props) {
  const [bgurl, setBgurl] = useState('')
  const refresh = useTimer(props.config.interval)
  const picStyle = {
    backgroundImage: 'url("'+bgurl+'")',
    width: props.size.width+'px',
    height: props.size.height+'px',
  }
  useEffect(()=>{
    const img = new Image()
    img.onload = ()=>{
      setBgurl(img.src)
    }
    img.src = getImage(props.config.sources[props.config.type], {
      width: props.size.width,
      height: props.size.height,
      spam: +refresh
    })
  }, [refresh, props])
  
  return (
    <div className='random-pic' style={picStyle}></div>
  )
}

export default RandomPic